  list-style: none;
  counter-reset: li-number; }

.about-us__item {
  position: relative;
  text-align: center; }
  .about-us__item::before {
    position: absolute;
    top: -40px;
    left: 50%;
    font-size: 28px;
    font-weight: 700;
    color: #0ad9c6;
    counter-increment: li-number;
    content: "0" counter(li-number); }

@media (max-width: 1023px) {
  .about-us h2 {
    margin-bottom: 80px;
    font-size: 24px; }
